.bb-studios-nav {
  padding-left: 80px;
  background: #1c1c1c; }
  .bb-studios-nav .nav-item {
    margin-right: 30px; }

.bb-header-icons {
  background: url(../assets/images/icons.png) no-repeat;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden; }

.bb-header-logo {
  background-color: #3d648b;
  width: 55px;
  height: 56px;
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
  line-height: 55px; }

.bb-studios-logo {
  background-position: 0 0;
  width: 29px;
  height: 29px;
  display: inline-block; }

.bb-icon-folder {
  background-position: -32px 0;
  width: 19px;
  height: 16px;
  margin-right: 15px; }

.bb-icon-eye {
  background-position: -66px -31px;
  width: 24px;
  height: 15px;
  margin-right: 15px; }

.bb-icon-arrow {
  background-position: -32px -19px;
  width: 11px;
  height: 7px;
  margin-left: 10px; }

.bb-studio-select {
  background-color: #3d538b;
  border-radius: 0;
  width: 100%;
  height: 58px;
  color: #fff;
  text-align: left;
  top: -10px;
  position: absolute; }
  .bb-studio-select:after {
    float: right;
    margin-top: 10px; }
  .bb-studio-select + .dropdown-menu {
    width: 100%;
    top: 45px; }

.iconPlusGray {
  background-position: -108px 0;
  width: 18px;
  height: 18px;
  margin-top: -10px; }

.iconFolderGray {
  background-position: -129px 0;
  width: 17px;
  height: 15px;
  margin-top: -9.5px; }

.iconTickGray {
  background-position: 0 -32px;
  width: 19px;
  height: 15px;
  margin-top: -9.5px; }

.iconSettingGray {
  background-position: -22px -32px;
  width: 18px;
  height: 17px;
  margin-top: -10.5px; }

.iconBuilder {
  background-position: -72px 0;
  width: 33px;
  height: 28px;
  float: left;
  margin-right: 20px; }

a {
  cursor: pointer; }

img.avatar {
  width: 25px;
  height: 25px;
  border-radius: 50%; }



